import React, { Component } from "react";
import { 
  Card, CardImg, CardText, CardBody, CardTitle, Button, 
  Breadcrumb, BreadcrumbItem, Modal, ModalHeader,
  ModalBody,
  Label, Col, Row
} from "reactstrap";
import { Link } from 'react-router-dom';
import { formatDate, toSentenceCase } from "../../shared/helpers";
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from '../LoadingComponent';
import { baseUrl } from '../../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';
console.log('baseUrl', baseUrl)

const required = val => val && val.length;
const maxLength = len => val => !val || (val.length <= len);
const minLength = len => val => val && (val.length >= len);
;

class CommentForm extends Component{
  constructor(props){
    super(props);
    this.state={
      isModalOpen: false,
      modalToggle: false
    }
    this.onSubmitCommentClick= this.onSubmitCommentClick.bind(this);
    this.closeModal= this.closeModal.bind(this);
  }
  
  handleSubmit(values) {
        this.props.addComment(this.props.campsiteId, values.rating, values.author, values.text);
    }
  
  onSubmitCommentClick() { 
    this.setState({
      isModalOpen: true
      
    })
  }
  
  closeModal() {
    this.setState({isModalOpen: false})
  }
  
  render(){
    return (
    <div>  
      <Button outline onClick={this.onSubmitCommentClick} type="submit">
        <i className="fa fa-pencil"> Submit Comment </i>
      </Button>
      <Modal isOpen={this.state.isModalOpen}>
                <ModalHeader toggle={this.closeModal}>Submit Comment</ModalHeader>
                <ModalBody>
                  <LocalForm onSubmit={values => this.handleSubmit(values)}>
                    <div className="form-group">
                        <Label htmlFor="rating">Rating</Label>
                            <Control.select className="form-control" model=".rating" id="rating" name="rating">
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                            </Control.select>
                    </div>
                    <div className="form-group">
                        <Label htmlFor="author">Author</Label>
                        
                            <Control.text model=".author" id="author" name="author"
                                placeholder="Author"
                                className="form-control"
                                validators={{
                                    required,
                                    minLength: minLength(2),
                                    maxLength: maxLength(15)
                                }}
                            />
                            <Errors
                                className="text-danger"
                                model=".author"
                                show="touched"
                                component="div"
                                messages={{
                                    required: 'Required',
                                    minLength: 'Must be at least 2 characters',
                                    maxLength: 'Must be 15 characters or less'
                                }}
                            />
                        
                    </div>
                    <div className="form-group">
                        <Label htmlFor="text">Comment</Label>
                        
                            <Control.textarea model=".text" id="text" name="text"
                                rows="6"
                                className="form-control"
                            />
                    </div>
                    <div className="form-group">
                            <Button type="submit" color="primary">
                                Send Feedback
                            </Button>
                    </div>
                  </LocalForm>
                </ModalBody>
            </Modal>
    </div>
    )
  }
}

  function RenderCampsite ({campsite}) {
    return(
      <div className="col-md-5 m-1">
            <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg top src={baseUrl + campsite.image} alt={campsite.name} />
                    <CardBody>
                        <CardText>{campsite.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        </div>
  );
    }
  function RenderComments({comments, postComment, campsiteId}){
    if (comments) {return (
      <div className="col-md-5 m-1">
        <h4>Comments</h4>
        <Stagger in>
            {comments.map(comment => {
                return (
                    <Fade in key={comment.id}>
                        <div>
                            <p>{comment.text}<br />
                                -- {comment.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}
                            </p>
                        </div>
                    </Fade>
                );
            })}
        </Stagger>
        <div>
          {comments.map(({ text, author, date }, i) => (
            <div key={i}>
              <p>{toSentenceCase(text)}</p>
              <p>
                -- {author}, 
                 {formatDate( date )}
              </p>
            </div>
          ))}
        </div>
        <CommentForm campsiteId={campsiteId} addComment={postComment} />
      </div>
    ) } return 
  }


  function CampsiteInfo(props) {
    if (props.isLoading) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        if (props.errMess) {
            return (
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <h4>{props.errMess}</h4>
                        </div>
                    </div>
                </div>
            );
        }
    if (props.campsite) {
        return (
            <div className="container">
              <div className="row">
                <div className="col">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/directory">Directory</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.campsite.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <h2>{props.campsite.name}</h2>
                    <hr />
                </div>
            </div>
              <div className="row">
                  <RenderCampsite campsite={props.campsite} />
                  <RenderComments 
                  comments={props.comments} 
                  addComment={props.postComment}
                  campsiteId={props.campsite.id}
                  />
              </div>
            </div>
        );
    }
    return <div />;
}
export default CampsiteInfo;
